@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

/* ul {
    list-style-type: none;
} */
.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

:root {
  --primary: rgb(255, 255, 255);
  --secondary: rgb(255, 255, 255);
  --errorColor: red;
  --stepNumber: 6;
  --containerWidth: 600px;
  --bgColor: rgb(232, 232, 232);
  --inputBorderColor: rgb(148, 66, 66);
}

body {
  font-family: "Roboto", sans-serif;
  color: #fff;
  font-size: 1rem;
}

h2 {
  font-weight: 200;
  color: #FFE404;
}

p {
  font-weight: 300;
  line-height: 1.4;
}

main {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
}
main .bg_img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: rotate(180deg);
  z-index: 1;
}
main .bg_img_overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #114CE3;
  mix-blend-mode: multiply;
  z-index: 2;
}
main .bg_img_overlay_dark {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #000;
  opacity: 0.65;
  z-index: 3;
}
main .info-container {
  display: none;
  position: relative;
  z-index: 4;
  padding: 30px;
  max-width: 600px;
  gap: 1em;
}
main .info-container .logo {
  padding-bottom: 0.5em;
  border-bottom: 1px solid #FFE404;
}
main .info-container .info {
  gap: 1em;
}
main .info-container .info ul {
  gap: 0.5em;
  line-height: 1.5;
}
main .info-container .form-info {
  gap: 1em;
}
main .info-container .form-info h2 {
  padding-bottom: 0.5em;
  border-bottom: 1px solid #FFE404;
}
main .info-container .form-button {
  margin-top: 1em;
  margin-left: auto;
  gap: 1em;
}
main .info-container .form-button button {
  background-color: #FFE404;
  border: none;
  padding: 6px 20px;
  text-transform: uppercase;
  cursor: pointer;
}
main .info-container .form-button button:hover, main .info-container .form-button button:active {
  background-color: #fff;
  color: #114CE3;
}

.active {
  display: flex !important;
}

.form_container {
  position: relative;
  z-index: 4;
  padding: 20px;
  max-width: 450px;
}
.form_container h2 {
  margin-bottom: 20px;
}

.form_slide {
  display: none !important;
  width: 100%;
}
.form_slide .basic_info {
  gap: 1em;
}
.form_slide input {
  width: 100%;
  margin: 10px 0 20px 0;
  font-size: 1rem;
  padding: 10px;
  outline: none;
  border: none;
  background: url("https://cdn-icons-png.flaticon.com/512/7420/7420995.png") no-repeat 95% 50% rgb(255, 255, 255);
  background-size: 20px;
}
.form_slide input:focus {
  border: none;
  outline: none;
}
.form_slide input:focus:invalid {
  background: url("https://cdn-icons-png.flaticon.com/512/2893/2893811.png") no-repeat 95% 50% rgb(255, 255, 255);
  background-size: 20px;
}
.form_slide input:required:focus:valid {
  background: url("https://cdn-icons-png.flaticon.com/512/1828/1828739.png") no-repeat 95% 50% rgb(255, 255, 255);
  background-size: 20px;
}
.form_slide input:required:valid {
  background: url("https://cdn-icons-png.flaticon.com/512/1828/1828739.png") no-repeat 95% 50% rgb(255, 255, 255);
  background-size: 20px;
}
.form_slide .not_required {
  background: #fff;
}
.form_slide .not_required:focus:invalid {
  background: #fff;
}
.form_slide select {
  width: 100%;
  margin: 10px 0 20px 0;
  font-size: 1rem;
  padding: 10px;
  outline: none;
  border: none;
  background: url("https://cdn-icons-png.flaticon.com/512/3416/3416111.png") 98% 50% no-repeat #fff;
  appearance: none;
  background-size: 20px;
}
.form_slide .checklist {
  display: flex;
  flex-wrap: wrap;
  margin: 0px 0 20px;
}
.form_slide .check {
  margin: 10px 20px 5px 0;
  display: flex;
  align-items: center;
  gap: 0.5em;
}
.form_slide .check input {
  width: unset;
  margin: unset;
}
.form_slide textarea {
  width: 100%;
  height: 60px;
  margin: 10px 0 20px 0;
  font-size: 1rem;
  padding: 10px;
  outline: none;
  border: none;
}
.form_slide .buttons {
  margin-top: 15px;
  display: flex;
  gap: 1em;
  width: 100%;
}
.form_slide .buttons button {
  background-color: #FFE404;
  border: none;
  padding: 6px 20px;
  text-transform: uppercase;
  cursor: pointer;
}
.form_slide .buttons button:hover, .form_slide .buttons button:active {
  background-color: #fff;
  color: #114CE3;
}

.visible {
  display: block !important;
}

.submitted_container {
  padding: 20px;
  position: relative;
  z-index: 4;
  max-width: 600px;
}
.submitted_container h2 {
  margin-bottom: 20px;
}